.dad2 {
  display: flex;
  justify-content: center;
  align-items: center;
}
.board {
  display: flex;
  align-items: center;
  flex-direction: column;
  min-width: 100px;
  min-height: 200px;
  border: 3px solid grey;
  border-radius: 12px;
  margin: 5px;
  &__title {
    font-size:1.2em;
    font-weight: 600;
  }
}
.item {
  width: 80%;
  border: 1px solid grey;
  padding:5px;
  border-radius:3px;
  margin: 3px 0;
  cursor: grab;
  background-color: darkslategray;
}
